import React from "react";

export const UserTable = ({users, setUserId, userId}) => {
    return (
        <table className="table is-narrow">
            <thead>
                <tr>
                    <th>#</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th></th>
                </tr>
            </thead>

            <tbody>
                {users.map((user) => {
                    return (
                        <tr key={user.id}>
                            <td>{user.id}</td>
                            <td>{user.name}</td>
                            <td>{user.email}</td>
                            <td>
                                {userId === user.id ? (
                                  <button className="button is-link"
                                    onClick={() => setUserId(user)}  
                                  >
                                    <span className="icon">
                                      <i className='bx bxs-heart-circle' ></i>
                                    </span>
                                  </button>
                                ) : (
                                  <button className="button"
                                    onClick={() => setUserId(user)}  
                                  >
                                    <span className="icon">
                                      <i className='bx bx-heart-circle'></i>
                                    </span>
                                </button>
                                )}
                            </td>
                        </tr>
                    )
                })}
            </tbody>
        </table>
    )
}

