import React from "react"

import { getUsers, getUser, getUserTodo } from "../../forUsers";


export class UserInfo extends React.Component {
    state = {
        user: null,
        todos: [],
        newTitleTodo: null,
    }

    async componentDidUpdate(prevProps) {
        if (prevProps.userId !== this.props.userId) {
            const [user, todos] = await Promise.all([
                getUser(this.props.userId),
                getUserTodo(this.props.userId)
            ]
            )
    
            this.setState({user, todos})
        }
    }

    inputChange = (event) => {
      const {value} = event.target;

      if (value !== ' ') {
        return this.setState({newTitleTodo: value})
      } else {
        return 0;
      } 
    }


    onHandleSubmit = () => {
        const newId = Math.max(...this.state.todos.map((todo) => todo.id + 1));

        const newTodo = {
            id: newId,
            completed: false,
            title: this.state.newTitleTodo,
        }

        console.log(newTodo);

        if (this.state.newTitleTodo) {
          this.setState(state => ({
            todos: [
              ...state.todos,
              newTodo,
            ]
          }))
        } else return 0;

      this.setState({newTitleTodo: ''})
    }

    render () {

        console.log(this.state.user);
        const { user, todos, newTitleTodo } = this.state;
        console.log(newTitleTodo);

        if (!user) {
            return <button className="button">No selected user yet</button>
        }

        return (
            <div className="card">
              <div className="card-content">
                <div className="media">
                    <div className="media-content">
                        <p className="title is-4">{user.name}</p>
                        <p className="subtitle is-6">{user.email}</p>
                    </div>
                </div>

                <div className="content">
                    <p className="title is-5">Todos</p>
                    <ul>
                      {todos.map((todo) => {
                        return (
                            <li key={todo.id}>
                                <label className="checkbox">
                                    <input type="checkbox" checked={todo.completed} onClick={!todo.completed}/>
                                    &nbsp;
                                     {todo.title}
                                </label>
                            </li>
                        )
                      })}

                      <input 
                        type="text" 
                        value={newTitleTodo}
                        onChange={this.inputChange}
                      />

                      <button onClick={this.onHandleSubmit}>Add</button>
                    </ul>
                </div>
              </div>
            </div>
        )
    }
}