import React from 'react';
import './App.scss';
import 'boxicons'
import 'bulma'
import classNames from 'classnames';

import { UserTable } from '../UserTable/UserTable';
import { UserInfo } from '../UserInfo/UserInfo';
import { request } from '../../helpers';
import { getUsers } from '../../forUsers';
import { CatchError } from '../CatchError/CatchError';

const url = 'https://jsonplaceholder.typicode.com/users'

// const getUsers = () => {
//   return request('users')
// }

class App extends React.Component {
  state = {
    users: [],
    userId: 0,
    loading: false,
    isLoadError: false,
    load: false
  }

  // componentDidMount() {
  //   getUsers()
  //     .then(users => this.setState({users}))
  // }

  load = async () => {
    this.setState({loading: true, isLoadError: false})
    try {
      const users = await getUsers();
      this.setState({users, load: true})
    } catch (error) {
      this.setState({isLoadError: true, loading: false})
    }
  }

  setUserId = (user) => {
    this.setState({userId: user.id})
  }

  render() {
    const { users, userId, loading, isLoadError, load } = this.state

    return (
      <section className='section'>
        <div className='container'>
          <h1 className='title'>Users Table</h1>
            <div className='columns'>
              <div className='column'>
                {users.length ===  0 ? (
                  <button 
                    className={classNames(
                      'button',
                      'is-link',
                      {'is-loading' : loading}
                    )}
                    onClick={this.load}>
                    Load
                  </button>
                ) : (
                  <UserTable users={users} setUserId={this.setUserId} userId={userId}/>
                )}
              </div>
                <UserInfo userId={userId} isLoad={load}/>
            </div>

            {isLoadError && <CatchError/>}
        </div>
      </section>
    )
  }
}
export default App;
