const BASE_URL = 'https://jsonplaceholder.typicode.com'


export const request = (option) => {
    return (
        fetch(`${BASE_URL}/${option}`)
          .then(responce => {
            if (!responce.ok) {
                throw new Error(`${responce.status} - ${responce.statusText}`)
            }

            return responce.json();
          })
    )
}